import React from "react";
import {MobilePropositionB2B} from "./MobilePropositionB2B";
import ClientContextCheckbox from "eshop/modules/configurator/components/ClientContextCheckbox";
import OnlineUtils from "eshop/utils/OnlineUtils";
import {FakeOfferBox} from "eshop/modules/configurator/components/lp/offers/FakeOfferBox";
import TooltipFromHtml from "../../../../core/components/ui/TooltipFromHtml";
import DiscountInfo from "eshop/modules/configurator/components/DiscountInfo";

export default class MobileCarouselB2B extends React.PureComponent {
    constructor(props) {
        super(props);
        this.onClickPickDevice = this.onClickPickDevice.bind(this);
        this.state = {isUpdating: false};
        OnlineUtils.setAsLastViewedOfferPage();
    }

    componentDidMount() {
        const that = this;
        this.loadModulesInsideCarousel();
        OPL.UI.on("module.started", function (data) {
            if (data.module === "common/modules/opl-carousel") {
                that.carouselReady();
                console.log("CAROUSEL STARTED");
            }
        });
    }

    carouselReady() {
        this.props.carouselReady();
    }

    getDefaultPropositionCarouselIndex() {
        console.log("this.props.selectedBaseRatePlanId", this.props.selectedBaseRatePlanId);
        if (!this.props.selectedBaseRatePlanId || !this.props.offers) {
            return;
        }
        const propositions = this.props.offers;
        const propositionIndexed = propositions.find(p => p.rateplanBaseProductId === this.props.selectedBaseRatePlanId);
        const goToIndex = propositions.indexOf(propositionIndexed);
        return goToIndex;
    }

    layoutFixer() {
        return (
            {
                path: "core/modules/layout-fixer",
                options: {
                    selectors: [
                        ".js-layout-fixer-group-1",
                        ".js-layout-fixer-group-2",
                        ".js-layout-fixer-group-3",
                        ".js-layout-fixer-group-4",
                        ".js-layout-fixer-group-5",
                        ".js-layout-fixer-group-6",
                    ],
                },
            }
        );
    }

    updateDimensions() {
        if (this.carouselRef) {
            OPL.UI.fire(OPL.UI.EVENTS.modules.layoutFixer.recalculate, this.carouselRef);
        }
    }

    oplCarousel(initial, offerCount) {
        return ({
            path: "common/modules/opl-carousel",
            options: {
                arrows: false,
                dots: true,
                centerMode: false,
                slidesToShow: 1,
                initialSlide: initial,
                mobileFirst: true,
                centerPadding: "5px",
                responsive: [
                    {
                        breakpoint: 567,
                        settings: {
                            slidesToShow: 2,
                            dots: true,
                        },
                    },
                    {
                        breakpoint: 1259,
                        settings: {
                            slidesToShow: offerCount,

                        },
                    },
                ],
            },
        });
    }

    jsModules(offerCount) {
        return ([
            this.oplCarousel(this.getDefaultPropositionCarouselIndex(), offerCount),
            this.layoutFixer(),
        ]);
    }

    loadModulesInsideCarousel(offerCount = 3) {
        if (offerCount === 1 || offerCount > 3) {
            OnlineUtils.isAssignment(this.props.processType) || OnlineUtils.isMnpApplication(this.props.processType) ? offerCount = 1 : offerCount = 4;
        }
        if (this.carouselRef) {
            OPL.UI.loadModules(this.carouselRef, this.jsModules(offerCount));
        }
    }

    shouldComponentUpdate(newProps, newState) {
        if (this.state.isUpdating || (newProps.isPropositionListCountSetMode && newProps.isPropositionListCountSetMode === this.props.isPropositionListCountSetMode))
            return false;
        return !(this.props.propositionListCount !== newProps.propositionListCount || this.props.propositionListCount === 0);
    }

    componentWillUpdate(newProps) {
        console.log("******************************offerB2BcarouselContainer componentWillUpdate ");

        let plotCombo = "";
        if (newProps.offers && !!newProps.offers[0]) {
            plotCombo = newProps.offers[0].offerType + "_" + newProps.offers[0].processType + "_" + newProps.offers[0].loyaltyLength;
        }

        if ( newProps.offers &&
            (!(!!this.props.offers) ||
                newProps.offers.length !== this.props.offers.length ||
                newProps.offers[0].processType !== this.props.offers[0].processType ||
                newProps.offers[0].loyaltyLength !== this.props.offers[0].loyaltyLength ||
                newProps.offers[0].offerType !== this.props.offers[0].offerType ||
                plotCombo != this.state.plotCombo ||
                newProps.propositionListCount !== this.props.propositionListCount
            )) {
            this.setState({isUpdating: true});
            if (document.getElementById("mobilePropositionCarouselContainer")) {
                console.log("SToP MODULES");
                OPL.UI.stopModules(document.getElementById("mobilePropositionCarouselContainer"));
                //  this.props.carouselPreparing();
            }
        }
    }

    componentDidUpdate() {
        if (this.state.isUpdating) {
            console.log("*******offerB2BcarouselContainer componentDidUpdate ");
            console.log("START MODULES ");
            let plotCombo = "";
            if (this.props.offers && !!this.props.offers[0]) {
                plotCombo = this.props.offers[0].offerType + "_" + this.props.offers[0].processType + "_" + this.props.offers[0].loyaltyLength;
            }
            this.loadModulesInsideCarousel(this.props.offers && this.props.offers.length);
            this.setState({isUpdating: false, plotCombo: plotCombo});
        }
        this.updateDimensions();
    }

    onClickPickDevice(e) {
        e.preventDefault();
        this.props.setPropositionListSoftBundleGroup();
        const returnLink = this.props.marketContextPrefixUrl+this.props.productListUrl;
        window.location = returnLink;
    }

    getDescriptionValueForSelectedOfferType = ({descriptions,selectedOfferType}, fieldName) => {
        return descriptions[[fieldName, selectedOfferType].filter(Boolean).join(".")] ||
            `No value found. Missing configuration for ${fieldName}.${selectedOfferType}`;
    };

    render() {
        if (this.props.propositionListCount === 0) {
            return null;
        }
        const offerDisclaimer = this.props.descriptions[["offerDisclaimer", this.props.selectedOfferType, this.props.processType].filter(Boolean).join(".")];
        const disclaimerElem = () => (offerDisclaimer ?
                <div className="g-black1-bg g-white1-c u-margin-l u-margin-top-l" key="disclaimer">
                    {TooltipFromHtml.conditionalRender(offerDisclaimer)}
                </div> :
                null
        );
        const discountInfoElem = () => {
            return this.props.isB2B ? null : (
                <DiscountInfo
                    key={"discountInfo"}
                    className={"u-padding-top-m"}
                    descriptions={this.props.descriptions}
                    contractRole={this.props.contractRole}
                    offerType={this.props.selectedOfferType}/>);
        };
        const renderHeaderAndClientContext = () => (
            !OnlineUtils.isAssignment(this.props.processType) &&
            !OnlineUtils.isMnpApplication(this.props.processType) &&
            <div className={"u-border-top u-no-margin u-padding-s u-padding-top-m"}
                    key={"mobileCarouselB2bHeader"}>
                <h3>{this.props.descriptions.header}</h3>
                <div className={"u-padding-top-m u-padding-l u-small-padding-s "}>
                    <ClientContextCheckbox
                        tooltip={this.getDescriptionValueForSelectedOfferType(this.props, "clientContextTooltip")}
                        label={this.props.descriptions.clientContextLabel}
                        channels={this.props.channels}
                        position={"left"}
                        key={"clientContextCheckBox"}/>
                </div>
            </div>
        );
        const renderMobilePropositionCarouselContainer = () => (
            <div ref={ref => this.ref = ref} id={"mobilePropositionCarouselContainer"} key={"carouselContainer"}>
                <div id={"offerB2Bcarousel"} ref={ref => this.carouselRef = ref}
                     className="opl-carousel opl-carousel--brand-dots l-row u-padding-top-l u-small-padding-top-l-xl"
                     key={"carouselBody"}>
                    {
                        (OnlineUtils.isAssignment(this.props.processType) || OnlineUtils.isMnpApplication(this.props.processType)) ?
                            <FakeOfferBox
                                proposition={this.props.offers[0]}
                                propositionTitle={OnlineUtils.isMnpApplication(this.props.processType) ? "Przeniesienie numeru bez umowy" : "Przekazanie usług"}
                                {...this.props}/> :
                            this.props.offers.map((offer, index) =>
                                <MobilePropositionB2B
                                    key={"proposition_" + offer.id + "_" + index}
                                    id={"proposition_" + offer.id}
                                    offer={offer}
                                    selected={(offer.id === this.props.selectedOfferId)}
                                    {...this.props}
                                    index={index}
                                    selectOfferCallback={this.props.selectOffer}
                                />,
                            )
                    }
                </div>
            </div>
        );
        if ((this.props.offers && this.props.offers[0])) {
            return (
                <div>
                    {
                        [
                            renderHeaderAndClientContext(),
                            disclaimerElem(),
                            discountInfoElem(),
                            renderMobilePropositionCarouselContainer(),
                        ]
                    }
                </div>
            );
        } else {
            return null;
        }
    }
}

